.main{
    padding: 15px;
    height: 100vh;
    
}

.layout{
    display: flex;
}

.sidebar{
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    background-color: #7752FE;
    box-shadow: 0 0 2px gray;
    margin-right: 30px;
    color: white;
    
}

.content{
    height: 100%;
    width: 100%;
}

.header{
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px gray;
    background-color: rgb(167, 170, 187);
    
}

.body{
    height: 90vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px gray;
    background-image: url("medical-background-cjge7e89adg6ub8x.jpg");
  
}

.logo h6{
    font-size: 2.4rem;
    text-align: center;
    margin: 20px 0px;
    color: rgb(217, 247, 49);
}

.menu{
    margin-top: 60px;
}
.menu-item{
    margin-top: 30px;
}

.menu-item a{
    color: white;
    text-decoration: none;
    font-size: 1.4rem;
}

.menu-item i{
    font-size: 1.4rem;
    margin: 0 15px;
}

.active{
    background-color: white;
    padding: 10px;
    
}

.active a, .active i {
    color: #7752FE;
}

.header-content{
    display: flex;
    align-items: center;
    height: 60px;
    justify-content: flex-end;
    margin: 0 10px;
}

.header-content i{
    margin-top: 7px;
    margin-right: 10px;
    font-size: 1.4rem;
}

.header-content a{
    font-size: 1.4rem;
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
    color: blue;
}